import { FaBars } from 'react-icons/fa';
import { NavLink as Link } from 'react-router-dom';
import styled from 'styled-components';

export const Nav = styled.nav`
  background: rgba(0,0,0,0.92);
  height: 70px;
  display: flex;
  justify-content: space-between;
  padding: 0.5rem calc((100vw - 1250px) / 2);
  font-size: 14px;
  line-height: 26px;
  // margin-top: 30px;
  margin-bottom: 10px;
`;

export const NavLink = styled(Link)`
  color: white;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 5px 20px;
  height: 100%;
  cursor: pointer;
  border: 1px solid transparent;
  border-radius: 30px;
  font-weight: 600;
  &.active {
    color: white;
    border-color: white;
  }
  &:hover {
    color: white;
    border-color: white;
    transition: color 300ms linear;
  }

  @media screen and (max-width: 990px) {
    // width: 100%;
    height: 70px;
  }
`;

export const NavLink2 = styled(Link)`
  color: white;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 5px 20px;
  height: 100%;
  cursor: pointer;
  border: 1px solid;
  border-radius: 30px;
  color: white;
  border-color: white;
  font-weight: 600;
  @media screen and (max-width: 990px) {
    // width: 100%;
    height: 70px;
  }
`;

export const Bars = styled(FaBars)`
  display: none;
  color: white;
  @media screen and (max-width: 990px) {
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 75%);
    font-size: 1.8rem;
    cursor: pointer;
  }
`;

export const NavMenu = styled.div`
  display: flex;
  align-items: center;
  margin-right: 190px; 
  @media screen and (max-width: 990px) {
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: ${({click}) => click ? 0 : "-100%"};
    flex-direction: column;
    transition: 0.5s all ease-in;
    background-color: rgba(0,0,0,0.92);
    z-index: 9;
  }
  // padding: 30px;
  padding-top: 40px;
  padding-bottom: 10px;
`;